.footer__copyright {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
  margin: 0;
}

@media screen and (max-width: 882px) {
  .footer__copyright {
    margin-left: 14px;
  }
}

@media screen and (max-width: 575px) {
  .footer__copyright {
    font-size: 14px;
    line-height: 17px;
  }
}